export default {
	data() {
		return {
			text:'该订单未进行确认收货，是否确认收货',
			isShow:false,
			inputValue: ""
		}
	},
	methods: {
		getList(){
			uni.navigateTo({
				url:'../userOrderDetile/orderList?orderSn=' + that.inputValue,
			})
		},
		showConfig(res){
			this.isShow=true
			this.inputValue=res
		},
		hideConfig(){
			this.inputValue=''
			this.isShow=false
		},
		fail(res){
			this.isShow=false
			this.inputValue=''  
		},
		succer(res){
			this.submit();
		},
		deliveryInput(e) {
			this.inputValue = e.detail.value
		},
		submit() {
			if(!this.inputValue){
				this.$common.toast('请填写完整的提货码')
				return
			}
			var that = this;
			uni.navigateTo({
				url: '/pages/userOrderDetile/userOrderDetile?orderSn=' + that.inputValue,
			})	
			// 去到列表		
			// uni.navigateTo({
			// 	url:'../userOrderDetile/orderList?orderSn=' + that.inputValue,
			// })
			that.inputValue = ''
		},
		scanCode() {
			var that = this;
			// 只允许通过相机扫码
			uni.scanCode({
				onlyFromCamera: true,
				success: function(res) {
					that.inputValue = res.result;
					that.submit();
				}
			});
		}
	}
}
